.asosiy {
    width: 100%;
    background-image: url('../../Assets/img/img1.jpg');
    background-repeat: no-repeat;
    background-size: cover;

    .asosiy_wrapper {
        max-width: 1200px;
        margin: auto;
        text-align: center;
        

        h1 {
            text-align: center;
            margin: auto;
            color: rgb(31, 20, 11);
            width: 670px;
            padding: 60px 0;
            font-family: "Lora", serif;
            font-size: 65px;
            font-style: italic;
            opacity: 0;
            animation: fadeIn 2s 1s forwards;
        }

        @keyframes fadeIn {
            to {
                opacity: 1;
            }
        }

        h1::before,
        h1::after {
            content: attr(data-text);
            position: absolute;
            width: 50%;
            height: 100%;
            top: 0;
        }

        h1::before {
            left: 0;
            text-align: right;
            transform: translateX(-100%);
            animation: slideInFromLeft 2s forwards;
        }

        h1::after {
            right: 0;
            text-align: left;
            transform: translateX(100%);
            animation: slideInFromRight 2s forwards;
        }

        @keyframes slideInFromLeft {
            to {
                transform: translateX(0);
            }
        }

        @keyframes slideInFromRight {
            to {
                transform: translateX(0);
            }
        }

        .cards {
            width: 100%;
            display: flex;
            justify-content: space-around;

            .card {
                width: 400px;
                text-align: center;
                border-radius: 40px 0 40px 0;
                box-shadow: 10px 12px 10px;
                backdrop-filter: blur(10px);



                img {
                    width: 80%;
                    height: 270px;
                    margin: 5px 0;
                }

                button {
                    width: 80%;
                    height: 35px;
                    margin: 10px 0;
                    padding: 5px 0;
                    color: rgb(31, 20, 11);
                    border: none;
                    border-radius: 5px;
                    box-shadow: 0 0 12px gray;
                    background: rgba(255, 255, 255, 0.3);
                    cursor: pointer;
                    font-family: "Lora", serif;
                    font-weight: 600;
                    font-size: 20px;
                    font-style: italic;
                    letter-spacing: 4px;

                    &:hover {
                        width: 82%;
                        height: 37px;
                        font-size: 22px;
                        margin-bottom: 3px;
                    }

                    &:active {
                        width: 80%;
                        height: 35px;
                        font-size: 20px;
                    }
                }
            }

            .card-left {
                animation: slideInFromLeft 2s forwards;
            }

            .card-right {
                animation: slideInFromRight 2s forwards;
            }

            @keyframes slideInFromLeft {
                0% {
                    transform: translateX(-100%);
                    opacity: 0;
                }

                100% {
                    transform: translateX(0);
                    opacity: 1;
                }
            }

            @keyframes slideInFromRight {
                0% {
                    transform: translateX(100%);
                    opacity: 0;
                }

                100% {
                    transform: translateX(0);
                    opacity: 1;
                }
            }
        }

        audio {
            width: 200px;
            height: 40px;
            margin: 50px 0 18px 0;
            opacity: 0;
            animation: fadeInAudio 2s forwards;

            source{
                background: none;
            }
        }

        @keyframes fadeInAudio {
            to {
                opacity: 1;
            }
        }
    }
}